import React from 'react';
import {createNativeStackNavigator} from '@react-navigation/native-stack';
import SListExamsScreen from '../screens/SListExamsScreen';
import SmartTestHomeScreen from '../screens/SmartTestHomeScreen';
import FreeTestScreen from '../screens/FreeTestScreen';
import StudentReportScreen from '../screens/StudentReportScreen';
import ListExamResultScreen from '../screens/ListExamResultScreen';
import StudentGuideScreen from '../screens/StudentGuideScreen';

export type MainStackParamList = {
  SListExamsScreen: undefined;
  SmartTestHomeScreen: undefined;
  FreeTestScreen: undefined;
  StudentReportScreen: undefined;
  ListExamResultScreen: undefined;
  StudentGuideScreen: undefined;
};

const Main = createNativeStackNavigator<MainStackParamList>();

const MainNavigator = () => {
  return (
    <Main.Navigator
      screenOptions={{
        headerShown: false,
      }}>
      <Main.Screen name="SmartTestHomeScreen" component={SmartTestHomeScreen} />
      <Main.Screen name="SListExamsScreen" component={SListExamsScreen} />
      <Main.Screen name="FreeTestScreen" component={FreeTestScreen} />
      <Main.Screen name="StudentReportScreen" component={StudentReportScreen} />
      <Main.Screen name="StudentGuideScreen" component={StudentGuideScreen} />

      <Main.Screen
        name="ListExamResultScreen"
        component={ListExamResultScreen}
      />
    </Main.Navigator>
  );
};

export default MainNavigator;
