/* eslint-disable react-native/no-inline-styles */
import React from 'react';
import {ScrollView, StyleSheet, TouchableOpacity, View} from 'react-native';
import {
  Appbar,
  Button,
  Divider,
  FAB,
  Icon,
  Text,
  TextInput,
  useTheme,
} from 'react-native-paper';
import {useAuthStore, buildLink} from '@smarttestpro/smart-common';

const FreeTestScreen = ({navigation}: any) => {
  const [visible, setVisible] = React.useState(false);
  const theme = useTheme();
  const [maDeThi, setMaDeThi] = React.useState('');
  const {accessToken, refreshToken} = useAuthStore();
  const enterFreeExam = () => {
    const baseUrl = 'https://smarttest.pro';
    const url = `/${buildLink(
      'test-taking/entrance-exam/:id/079dd2ea-dc4c-4faa-b93e-8eb16935c98f',
      {
        id: maDeThi,
      },
    )}`;
    const uri = `${baseUrl}/thanh-vien/dang-nhap?return_to=${encodeURI(
      url,
    )}&accessToken=${encodeURI(accessToken || '')}&refreshToken=${encodeURI(
      refreshToken || '',
    )}`;
    navigation.push('TestExamWebScreen', {
      uri,
    });
  };

  return (
    <View style={styles.container}>
      <Appbar.Header style={{backgroundColor: theme.colors.primary}}>
        <Appbar.BackAction
          onPress={() => navigation.navigate('SmartTestHomeScreen')}
        />
        <Appbar.Content title="Thi tự do" color="white" />
      </Appbar.Header>
      <Divider />
      <View>
        <TextInput
          label="Mã đề thi"
          value={maDeThi}
          onChangeText={text => setMaDeThi(text)}
          right={
            <TextInput.Icon
              onPress={() => enterFreeExam()}
              icon="file-document-edit"
            />
          }
        />
      </View>
    </View>
  );
};
const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
});

export default FreeTestScreen;
