/* eslint-disable react/no-unstable-nested-components */
/* eslint-disable react-native/no-inline-styles */
import React from 'react';
import {
  FlatList,
  ScrollView,
  StyleSheet,
  TouchableOpacity,
  View,
} from 'react-native';
import {
  Appbar,
  Button,
  Divider,
  Icon,
  List,
  Text,
  Modal,
  useTheme,
  PaperProvider,
  Portal,
} from 'react-native-paper';
import {
  useAuthStore,
  IListExamResult,
  buildLink,
  formatDateTime,
} from '@smarttestpro/smart-common';
import {useExamResultStore} from '../stores';

const ListExamResultScreen = ({navigation}: any) => {
  const [visible, setVisible] = React.useState(false);

  const hideModal = () => setVisible(false);
  const {listExamResult, examResultDetail, getExamResultDetail} =
    useExamResultStore();
  const theme = useTheme();

  const containerStyle = {backgroundColor: 'white', padding: 20};

  const onViewExamResultDetail = (maKiemTra: string) => {
    getExamResultDetail(maKiemTra);
    console.log(examResultDetail);
    setVisible(true);
  };

  const renderItem = ({item}: {item: IListExamResult}) => (
    <List.Item
      title={item.noiDungDeThi}
      description={
        <View style={{display: 'flex', flexDirection: 'column'}}>
          <View
            style={{
              paddingTop: 8,
              display: 'flex',
              flexDirection: 'row',
              alignItems: 'center',
              gap: 4,
            }}>
            <Icon source="check-circle-outline" size={20} />
            <Text style={{color: theme.colors.secondary}}>
              {item.diem} điểm
            </Text>
          </View>
          <View
            style={{
              paddingTop: 8,
              display: 'flex',
              flexDirection: 'row',
              alignItems: 'center',
              gap: 4,
            }}>
            <Icon source="calendar-month-outline" size={20} />
            <Text style={{color: theme.colors.secondary}}>
              {formatDateTime(item.ngayKiemTra)}
            </Text>
          </View>
        </View>
      }
      style={{
        borderBottomColor: theme.colors.elevation.level5,
        borderBottomWidth: 1,
      }}
      right={() => (
        <View
          style={{
            paddingTop: 8,
            display: 'flex',
            flexDirection: 'column',
            gap: 8,
          }}>
          <Button onPress={() => onViewExamResultDetail(item.maKiemTra)}>
            Chi tiết
          </Button>
        </View>
      )}
    />
  );

  const renderChiTietKiemTra = ({item}: {item: any}) => (
    <List.Item
      title={item.noiDungDeThi}
      description={
        <View style={{display: 'flex', flexDirection: 'column'}}>
          <View
            style={{
              paddingTop: 8,
              display: 'flex',
              flexDirection: 'row',
              alignItems: 'center',
              gap: 4,
            }}>
            <Text style={{color: theme.colors.secondary}}>
              Câu {item.thuTuCau}:
            </Text>
            <Text style={{color: theme.colors.secondary}}>
              {item.ktStatus === true ? 'Đúng' : 'Sai'}
            </Text>
          </View>
        </View>
      }
      style={{
        borderBottomColor: theme.colors.elevation.level5,
        borderBottomWidth: 1,
      }}
    />
  );

  return (
    <PaperProvider>
      <Portal>
        <View style={styles.container}>
          <Appbar.Header style={{backgroundColor: theme.colors.primary}}>
            <Appbar.BackAction
              onPress={() => navigation.navigate('SListExamsScreen')}
            />
            <Appbar.Content title="Kết quả kiểm tra" color="white" />
          </Appbar.Header>
          <Divider bold />
          <View>
            <FlatList
              data={listExamResult}
              renderItem={renderItem}
              keyExtractor={item => item.maKiemTra}
            />
          </View>
          <Modal
            visible={visible}
            onDismiss={hideModal}
            contentContainerStyle={containerStyle}>
            <Text>{examResultDetail.tenDeThi}</Text>
            <Text>Điểm: {examResultDetail.diem}</Text>
            <View>
              <FlatList
                data={examResultDetail.danhSachChiTietKiemTra?.sort(
                  (a, b) =>
                    // eslint-disable-next-line radix
                    parseInt(a.thuTuCau || '0') - parseInt(b.thuTuCau || '0'),
                )}
                renderItem={renderChiTietKiemTra}
                keyExtractor={item => item.thuTuCau}
              />
            </View>
          </Modal>
        </View>
      </Portal>
    </PaperProvider>
  );
};
const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
});

export default ListExamResultScreen;
